import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import { Button, Divider } from "@material-ui/core";
import Branches from "../../../pages/Branches";


const drawerWidth = "20%";

const useStyles = makeStyles(theme => ({
    drawerPaper: {
        width: drawerWidth
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0
    },
    card: {
        justifyContent: "center",
        alignItems: "center",
        display: "flex",
        flexDirection: "column"
    },
    typo: {
        fontWeight: "800",
        fontSize: "32px",
        alignItems: "center",
        justifyContent: "center",
        display: "flex",
        flexDirection: "row",
        marginTop: "30px"
    },
    space: {
        margin: 30
    },
    button: {
        flexGrow: 1,
        display: "block",
    },
    options: {
        display: "flex",
        flexDirection: "space-around"
    },
    toolbar: theme.mixins.toolbar
}));

export default function CustomDrawer(props) {
    const classes = useStyles();
    const [listProj, setListProj] = useState([]);

    const Abstract = require("abstract-sdk");
    useEffect(() => {

        async function fetchData() {
            const client = new Abstract.Client({
                accessToken: "b2a8f44a196180a5055d694c3cc1fea6117f91759b7b86410b539d6b64050c30"
            });
            const projects = await client.projects.list();
            setListProj(projects);
        }
        fetchData();
    }, []);

    return (
        <Drawer
            className={classes.drawer}
            variant="permanent"
            color="inherit"
            classes={{
                paper: classes.drawerPaper
            }}
        >
            <div className={classes.toolbar} />
            <div className={classes.space}>
                <div className={classes.typo}>Sketch-Selectify</div>
                <h3>Projects</h3>
                {listProj.map(project => (
                    <div key={project.id}>
                        <Button onClick={() => props.refetch(project.id)}>{project.name}</Button>
                        <Divider />
                    </div>
                ))}
            </div>
        </Drawer>
    );
}
