export default function CustomAppBar(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <CssBaseline />
            <AppBar position="fixed" style={{ backgroundColor: '#f5f8fa' }} className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <img src={Zoro} />
                    <Typography variant="h6" className={classes.flex} noWrap>
                        Sketch-Selectify
                    </Typography>
                    <Tabs className={classes.flex} value={value} onChange={handleChange} aria-label="simple tabs example">
                        <Tab label="Projects" {...a11yProps(0)} />
                        <Tab label="Components" {...a11yProps(1)} />
                    </Tabs>
                    <div className={classes.search}>
                        <div className={classes.searchIcon}>
                            <SearchIcon />
                        </div>
                        <InputBase
                            onChange={event => props.search(event.target.value)}
                            placeholder="Search library"
                            classes={{
                                root: classes.inputRoot,
                                input: classes.inputInput,
                            }}
                            inputProps={{ 'aria-label': 'search' }}
                        />
                    </div>
                </Toolbar>
            </AppBar>
        </div >
    );

}