const LandingPage = () => {
    const classes = useStyles();
    const [listProj, setListProj] = useState([]);
    const [showProj, setShowProj] = useState([]);
    const [status, setStatus] = useState("PROJS");
    const [val, setVal] = useState();
    const [searchTerm, setSearchTerm] = useState("");

    const Abstract = require("abstract-sdk");
    useEffect(() => {

        async function fetchData() {
            const client = new Abstract.Client({
                accessToken: "b2a8f44a196180a5055d694c3cc1fea6117f91759b7b86410b539d6b64050c30"
            });
            const projects = await client.projects.list();
            setListProj(projects);
            setShowProj(projects);
        }
        fetchData();
    }, []);

    useEffect(() => {
        setShowProj(listProj.filter(x => x.name.toLowerCase().includes(searchTerm.toLowerCase())))
    }, [searchTerm]);
    function setStatusFunc(x) {
        setStatus("BRANCHES");
        setVal(x);
    }
    if (status === "BRANCHES") {
        return <Branches projId={val} />
    }

    return (
        <div>
            <CustomAppBar search={(x) => setSearchTerm(x)} />
            <Toolbar />
            <div className={classes.body} style={{ background: "#2D3E50", maxWidth: "100vw" }}>
                <div className={classes.div2}>
                    <img className={classes.img} src="//static.hsappstatic.net/ui-library-public/static-1.91/img/canvas-logo.svg" />
                </div>
                <div className={classes.textStyle}>Component Library</div>
            </div>

            <div className={classes.body}>
                <div className={classes.secondDiv}>
                    <div className={classes.text}>
                        <h1>Welcome to Component Library</h1>
                    </div>
                    <div className={classes.smallText}>
                        <p>
                            HubSpot Canvas is the design system that we at HubSpot use to build our products. This library showcases the building blocks that make up our design system, from colors and typography to React-based components and data visualization tools. What you see here is a subset of our components and styles, pulled straight from our production code.
                        </p>
                        <p>
                            This library is a window into how we build our products here at HubSpot and what it’s like to build the HubSpot product. We’re sharing it because we’re proud of the time and effort we’ve put into creating our design system and optimizing it for developers and designers so that we can keep it evergreen.
                    </p>
                        <p>
                            If you’re an engineer or designer and you’re excited by what you see here, get in touch.
                    </p>
                    </div>
                </div>

                <div className={classes.space}>
                    <p className={classes.heading}>Explore Zorro Component Library:</p>
                </div>
                <div className={classes.cards}>
                    {showProj.map(project => (
                        <CustomCard key={project.id} projId={project.id} callback={(x) => setStatusFunc(x)} projName={project.name} />
                    ))}
                </div>
            </div>
            <div className={classes.footer}>
                <div className={classes.footerLine} />
                <div className={classes.copyRight}>
                    Copyright © 2018 – 2020 Zemoso Technologies Pvt Lmt, Inc.
                </div>
            </div>
        </div>
    );
};

export default LandingPage;
