import React, { useEffect, useState } from 'react';
import { Container, Toolbar, makeStyles } from "@material-ui/core";
import CustomAppBar from "../../components/Molecules/CustomAppBar";
import CustomDrawer from "../../components/Molecules/CustomDrawer";

const useStyles = makeStyles(theme => ({
    holder: {
        marginTop: 100,
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        textAlign: "center"
    },
    container: {
        display: "flex",
        flexDirection: "row",
        flexGrow: 1
    },
    usertitle: {
        paddingTop: theme.spacing(3),
        textAlign: "center"
    }
}));

export default function Branches(props) {
    const classes = useStyles();
    const [listBranches, setListBranches] = useState([]);
    const [currBranch, setcurrBranch] = useState(props.projId)
    const Abstract = require("abstract-sdk");
    useEffect(() => {
        async function fetchData() {
            const client = new Abstract.Client({
                accessToken: "b2a8f44a196180a5055d694c3cc1fea6117f91759b7b86410b539d6b64050c30"
            });
            const branches = await client.branches.list({
                projectId: currBranch
            });
            setListBranches(branches);
        }
        fetchData();
    }, [currBranch]);

    return (
        <div>
            <CustomAppBar />
            <Toolbar />
            <Container className={classes.container}>
                <CustomDrawer refetch={(id) => setcurrBranch(id)} />
                <div>{listBranches.map(branch => <div key={branch.id}>{branch.name}</div>)}</div>
            </Container>
        </div>
    );
}