import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { CardContent } from "@material-ui/core";
import Tool1 from "../../../assets/tool1.svg";
import Tool2 from "../../../assets/tool2.svg";
import Tool3 from "../../../assets/tool3.svg";
import '../../../App.css';

const useStyles = makeStyles(theme => ({
    card: {
        maxWidth: "100%",
        height: "200px",
        width: "200px",
        marginLeft: 15,
        marginBottom: 15,
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center",
        display: "flex",
        flexDirection: "column",
        transitionDuration: "150ms",
        boxShadow: "0 0 0 1px #7fd1de",
        transitionDuration: "500ms",
        '&:hover': {
            boxShadow: "0 0 0 3px #7fd1de, 0 0 12px 0 rgba(0,163,189,.3)",
            transitionDuration: "500ms"
        }
    },


    title: {
        fontSize: "16px",
        fontFamily: 'Avenir 95 Black',
        fontWeight: 400,
        lineHeight: "24px"
    },
    ques: {
        fontSize: 15
    },
    media: {
        height: 80,
        width: 80,
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center",
    },
    secDiv: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center"
    },
}));

export default function CustomCard(props) {
    const classes = useStyles();
    const images = [Tool1, Tool2, Tool3];

    var randomInt = Math.floor(Math.random() * images.length);
    var randomImage = images[randomInt];


    return (
        <div className={classes.card} style={{ borderTopColor: "rgb(127, 209, 222)" }}>
            <div class="border">
                <div className={classes.secDiv} onClick={() => props.callback(props.projId)}>
                    <CardContent />
                    <div>
                        <img className={classes.media} src={randomImage} alt="Tool1" />
                    </div>
                    <div>
                        <div
                            style={{ marginTop: "20px" }}
                            className={classes.title}
                        >
                            {props.projName}
                        </div>
                    </div>
                    <CardContent />
                </div>
            </div>
        </div>
    );

}
